// metamask
import Vue from 'vue'

const state = {
  currentAccount: '', // string  account address
  status: 0, // metamask0 ;1 2  , 3 4  5  window.web3
  commitMark: 0, // statusstatus; 
  whiteStatus: null, // , true:   false: 
  urlStatus: 6 // 0: url, 1: url, 6: 
}

const mutations = {
  setCurrentAccount (state, address) {
    state.currentAccount = address
  },
  setStatus (state, status) {
    state.status = status
  },
  setCommitMark (state, commitMark) {
    state.commitMark = commitMark
  },
  setWhiteStatus (state, whiteStatus) {
    state.whiteStatus = whiteStatus
  },
  setUrlStatus (state, urlStatus) {
    state.urlStatus = urlStatus
  }
}

const actions = {
  async setCurrentAccount ({state, commit}, address) {
    const oldAddress = state.currentAccount
    commit('setCurrentAccount', address)
    if (address && address !== oldAddress) {
      // 
      // const isWhite = store.state.countDown.status
      // url
      let url = window.location.href
      const index = url.indexOf('?to=')
      let urlParams = url.slice(index) // url;
      if (urlParams && urlParams.substr(0, 4) === '?to=') {
        const referralAddress = urlParams.substr(4) // buyreferralAddress
        if (referralAddress) { // url
          // 
          const myProvider = Vue.prototype.$myProvider
          const networkVersion = myProvider.networkVersion
          if (networkVersion === Vue.prototype.$ethConf.networkVersion) {
            const contractTeam = Vue.prototype.$myContracts && Vue.prototype.$myContracts.contractTeam
            if (contractTeam) {
              contractTeam.methods.isWhitelistAddress(address).call({}, function (error, result) {
                if (error) {
                  console.error(error)
                } else {
                  commit('setWhiteStatus', result)
                }
              })
            }
          }
        }
      }
    }
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}



