// metamask
import store from '../store/index'
import Vue from 'vue'
import Web3 from 'web3'

export default async function () {
  Vue.prototype.$metamaskMark += 1
  // todo 
  if (typeof window.ethereum !== 'undefined') {
    const provider = window['ethereum'] // metamask
    Vue.prototype.$myProvider = provider
    const myWeb3 = new Web3(provider) // web3 vuex
    // todo url
    // #vuehashlocation.search,history;
    let url = window.location.href
    const index = url.indexOf('?to=')
    let urlParams = url.slice(index) // url;
    let referralAddress = null // 
    if (urlParams && urlParams.substr(0, 4) === '?to=') {
      referralAddress = urlParams.substr(4) // buyreferralAddress
      if (referralAddress && referralAddress.length === 42) {
        store.commit('metamask/setUrlStatus', 1)
      } else {
        store.commit('metamask/setUrlStatus', 0)
      }
    } else {
      store.commit('metamask/setUrlStatus', 0)
    }
    try {
      // todo 
      const contractRes = new myWeb3.eth.Contract(Vue.prototype.$ethConf.contractRes.abi, Vue.prototype.$ethConf.contractRes.address)
      const contractTeam = new myWeb3.eth.Contract(Vue.prototype.$ethConf.contractTeam.abi, Vue.prototype.$ethConf.contractTeam.address)
      const contractRec = new myWeb3.eth.Contract(Vue.prototype.$ethConf.contractRec.abi, Vue.prototype.$ethConf.contractRec.address)
      const contractTerm = new myWeb3.eth.Contract(Vue.prototype.$ethConf.contractTerm.abi, Vue.prototype.$ethConf.contractTerm.address)
      Vue.prototype.$myWeb3 = myWeb3 // web3
      Vue.prototype.$myContracts = {contractRes, contractTeam, contractRec, contractTerm} // contract
    } catch (error) {
      console.error('myContract', error)
    }
    // store.commit('metamask/setProvider', provider) // metamaskvuex
    // store.commit('metamask/setNetworkVersion', provider.networkVersion)
    let isUnlocaked = true
    try {
      isUnlocaked = await provider._metamask.isUnlocked()
    } catch (error) {
      console.error('metamaskapi', error)
    }
    if (!isUnlocaked) {
      const status = 3
      const commitMark = store.state.metamask.commitMark + 1
      store.commit('metamask/setStatus', status)
      store.dispatch('metamask/setCurrentAccount', '') // actions
      store.commit('metamask/setCommitMark', commitMark)
      return status
    } else {
      try {
        const accounts = await provider.enable()
        const status = 1
        const commitMark = store.state.metamask.commitMark + 1
        store.dispatch('metamask/setCurrentAccount', accounts[0]) // actions
        store.commit('metamask/setStatus', status)
        store.commit('metamask/setCommitMark', commitMark)
        provider.on('accountsChanged', onAccounts) // 
        // provider.on('networkChanged', onNetwork) // networkVersion
        return status
      } catch (reason) {
        const status = 2
        const commitMark = store.state.metamask.commitMark + 1
        store.commit('metamask/setStatus', status)
        store.dispatch('metamask/setCurrentAccount', '') // actions
        store.commit('metamask/setCommitMark', commitMark)
        console.error('')
        return status
      }
    }
  } else if (typeof window.web3 !== 'undefined') {
    const status = 5
    const commitMark = store.state.metamask.commitMark + 1
    store.commit('metamask/setStatus', status)
    store.dispatch('metamask/setCurrentAccount', '') // actions
    store.commit('metamask/setCommitMark', commitMark)
    return status // metamask
  } else {
    const status = 4
    const commitMark = store.state.metamask.commitMark + 1
    store.commit('metamask/setStatus', status)
    store.dispatch('metamask/setCurrentAccount', '') // actions
    store.commit('metamask/setCommitMark', commitMark)
    return status // metamask
  }
}

function onAccounts (accounts) { // metamask
  console.warn('', accounts[0])
  // web3
  // Vue.prototype.$myWeb3 = new Web3(Vue.prototype.$myProvider) todo web3
  // vuexmetamask
  const status = 1
  const commitMark = store.state.metamask.commitMark + 1
  store.commit('metamask/setStatus', status)
  store.dispatch('metamask/setCurrentAccount', accounts[0]) // actions
  store.commit('metamask/setCommitMark', commitMark)
}

